:root {
  --yellow-color: #ffe550;
  --light-green-color: #bbf330;
  --dark-green-color: #7eb101;
  --super-dark-green: #597d00;
  --white-color: #fff;
  --black-color: #2c2c2c;
  --yellow-color: #ffe550;
  --green-color-hover: #b5ff00;
  --light-green-hover: #b5ff00;
  --red-color-error: #ff705d;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

.modal__btn-submit {
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 30px;
  font-family: inherit;
  font-size: 14px;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  color: var(--black-color);
  margin: 0;
  font-family: Roboto, Regular, sans-serif;
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
}

img {
  display: inline-block;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

