  left: 16px;
  transform: scale(.8);
}

@media screen and (min-width: 768px) {
  .subscribe-form__notification {
    top: calc(100% - 15px);
  }
}

.subscribe-form__field:invalid:required:not(:placeholder-shown) ~ .subscribe-form__icon, .subscribe-form__field:invalid:required:not(:placeholder-shown) ~ .subscribe-form__notification {
