  padding-left: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
}

.register-form__item {
  transition-property: border-color;
  position: relative;
}

.register-form__item:hover .register-form__input, .register-form__item:focus-within .register-form__input {
  border-color: var(--accent-cl);
  transition: border-color var(--anim);
  cursor: pointer;
}

.register-form__item:hover .register-form__icon, .register-form__item:focus-within .register-form__icon {
  fill: var(--accent-cl);
  transition: fill var(--anim);
  cursor: pointer;
}

.register-form__icon {
  width: 18px;
  height: 18px;
  transition: fill var(--anim);
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}

.register-form__message:focus, .register-form__message:hover {
  border: 1px solid var(--accent-cl);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
}

.register-form__agreement {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-bottom: 30px;
  display: inline-flex;
}

.register-form .register-icon-uncheck, .register-form .register-icon-check {
  width: 16px;
  height: 15px;
}

.register-form .register-checkbox:checked + .register-icon .register-icon-uncheck {
  opacity: 0;
  transition: opacity var(--anim);
}

.register-form .register-icon-check {
  opacity: 0;
  fill: var(--accent-cl);
  transition-property: all;
  transition: opacity var(--anim);
  transition: fill var(--anim);
}

.register-form .register-icon-check:focus, .register-form .register-icon-check:hover {
  fill: var(--accent-cl);
  cursor: pointer;
}

.register-form .register-checkbox:checked + .register-icon .register-icon-check {
  opacity: 1;
}

.register-form__agreement-text {
  text-align: center;
  letter-spacing: .03em;
